import React, { useEffect, useState } from 'react';

import { languages } from '../../resources/content/content_setup';

const LanguageSelector = () => {
    const [dropHeight, setDropHeight] = useState(0);

    useEffect(() => {
        
    }, []);

    const langSelect = (lang) => {
        window.sessionStorage.setItem('language', JSON.stringify(lang));
        window.location.reload(false);
    };

    const onMouseOver = (e, value) => {
        const container = e.target;
        const drop = document.getElementById('lang-sel-drop');
        let totalHeight = 0;

        if(value === true) {
            for(let i = 0; i < drop.children.length; i++) {
                totalHeight += drop.children[i].offsetHeight;
            }

            const padding = 20, gap = drop.children.length * 20, offset = 5;
            const avg = totalHeight / drop.children.length;
    
            totalHeight += avg + padding + gap + offset;
        }

        setDropHeight(totalHeight);
    }

    return (
        <div id='language-selector' onMouseEnter={(e) => onMouseOver(e, true)} onMouseLeave={(e) => onMouseOver(e, false)}>
            <img src={languages.selected.flag} />
            
            <div id='lang-sel-drop' className='dropdown' style={{'height': `${dropHeight}px`}}>
            {
                languages.list.map(el =>
                    el.lang !== languages.selected.lang ?
                    <div className='flag-container'>
                        <img src={el.flag} onClick={() => langSelect(el)} />
                    </div>
                    : ''
                )
            }
            </div>
        </div>
    );
};

export default LanguageSelector;
