import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import Image from 'react-bootstrap/Image';

import SVG from '../images/SVG';

import { svg as logo } from '../../resources/images/logo/logo';
import { svg as apricusGastrobar } from '../../resources/images/logo/apricus_gastrobar';

const TopSlider = () => {
    return (
        <div id='topslider'>
            <div className='slideImageShade' />
            <Carousel
                    className='carousel-container' 
                    fade={false}
                    touch={true}>
                {
                    ["#4c686b", "#701247", "#32a852"].map(el =>
                        <Carousel.Item 
                                interval={1000000} 
                                key={el} 
                                style={{ backgroundColor: el }}>

                            <div className='logo'>
                                <SVG classNames={'logo-svg'} svg={logo} />
                                <SVG classNames={'logo-svg'} svg={apricusGastrobar} />
                            </div>

                            <Carousel.Caption>
                                <div className='page-container'>
                                    <div>
                                        <h3>Slide Image {el}</h3>
                                    </div>
                                </div>
                            </Carousel.Caption>
                        </Carousel.Item>
                    )
                }
            </Carousel>
        </div>
    )
}

export default TopSlider
