import React from 'react';
import { Link as RLink } from "react-router-dom";

import CustomList from '../common/CustomList';
import { siteContent } from '../../resources/content/content_setup';
import { scrollTo } from '../../scripts/scrolling';

const Navbar = ({listOrientation}) => {
    const items = [
        {
            route: '/',
            name: siteContent['home']['title'],
            jsx:
                <RLink to='/' onClick={() => scrollTo()}>
                    { siteContent['home']['title'] }
                </RLink>
        },
        {
            route: '/menu',
            name: siteContent['menu']['title'],
            jsx:
                <RLink to='/menu' onClick={() => scrollTo()}>
                    { siteContent['menu']['title'] }
                </RLink>
        },
        {
            route: '/about',
            name: siteContent['about']['title'],
            jsx:
                <RLink to='/about' onClick={() => scrollTo()}>
                    { siteContent['about']['title'] }
                </RLink>
        },
        {
            route: '/contact',
            name: siteContent['contact']['title'],
            jsx:
                <RLink to='/contact' onClick={() => scrollTo()}>
                    { siteContent['contact']['title'] }
                </RLink>
        },
    ];

    return (
        <CustomList items={items} listOrientation={listOrientation} />
    )
}

export default Navbar
