import React, { useState, useEffect } from 'react';
import { Row, Col } from 'react-bootstrap';
import { VscListUnordered, VscListFlat } from 'react-icons/vsc';

import { siteContent } from '../../resources/content/content_setup';

const Menu = () => {
    const [showMenuAsTabs, setDisplayType] = useState(true);

    return (
        <div id='menu-container'>
            <div className='page-container'>
                <div>
                    <Row>
                        <Col>
                            <h1>{ siteContent['menu']['title'] }</h1>
                        </Col>

                        <Col className='menu-display-type'>
                            <VscListFlat onClick={() => setDisplayType(true)} />
                            <VscListUnordered onClick={() => setDisplayType(false)} />
                        </Col>
                    </Row>

                    <div className={showMenuAsTabs === true ? 'tab-view-container' : ''}>
                    {
                        showMenuAsTabs === true ?
                        <TabView siteContent={siteContent} />
                        :
                        <VerticalDisplay siteContent={siteContent} />
                    }
                    </div>
                </div>
            </div>
        </div>
    )
};

const TabView = ({ siteContent }) => {
    const [activeTabIndex, setActiveTabIndex] = useState(0);
    const [tabContainerHeight, setTabContainerHeight] = useState(0);

    useEffect(() => {
        const tabs = document.getElementsByClassName('tab-content active');
        let max = 0;
        
        for (var i = 0; i < tabs.length; i++) {
            const value = tabs[i].offsetHeight;
            if(value > max)
                max = value;
        }

        setTabContainerHeight(max);
    }, [activeTabIndex]);

    return (
        <>
            <div className='tab-selectors-container'>
                {
                    siteContent['menu']['menus'].map((el, index) => 
                        <button className={index === activeTabIndex ? 'active': ''} onClick={() => setActiveTabIndex(index)}>
                            <p className='text-container one-line marginless-el'>
                                { el.title }
                            </p>
                        </button>
                    )
                }
            </div>

            <div id='tabs-content-container' style={{'height': `${tabContainerHeight}px`}}>
            {
                siteContent['menu']['menus'].map((el, index) => 
                    <div className={`tab-content ${index === activeTabIndex ? 'active': ''}`}>
                        <ProductCategory content={el} />
                    </div>
                )
            }
            </div>
        </>
    )
};

const VerticalDisplay = ({ siteContent }) => {
    return (
        <>
        {
            siteContent['menu']['menus'].map(el => 
                <Row>
                    <Col>
                        <ProductCategory content={el} />
                    </Col>
                </Row>
            )
        }
        </>
    )
};

const ProductCategory = ({ content }) => {
    return (
        <>
        <h2>
            {
                content.title
            }
        </h2>

        {
            content['products'].map(prod => 
                <Row>
                    <Col>
                        <p>{ prod['name'] }</p>
                    </Col>
                    <Col>
                        <p>{ prod['price'] }</p>
                    </Col>
                </Row>
            )
        }
        </>
    )
};

export default Menu;
