import React, { useState } from 'react'

import Navbar from './Navbar';
import LanguageSelector from './LanguageSelector';
import Hamburger from './Hamburger';
import Sidenav from './Sidenav';

const TopBar = () => {
    const [sidenavToggle, setSidenavToggle] = useState(false);

    const onSidenavToggle = () => {
        setSidenavToggle(!sidenavToggle);
    };

    return (
        <div id='topbar' className='prevent-text-selection'>
            <LanguageSelector />

            <div className='page-container'>
                <div>
                    <Hamburger id={'hamb-menu'} toggleHamburger={sidenavToggle} onSidenavToggle={onSidenavToggle} />
                    <div id='navbar'>
                        <Navbar listOrientation={'h'}  />
                    </div>
                    <Sidenav listOrientation={'v'} sidenavToggle={sidenavToggle} />
                </div>
            </div>
        </div>
    )
}

export default TopBar
