import languagesJson from './languages.json'

const initLanguages = () => {
    let languages = {
        list: [],
        selected: {}
    };
    let selectedLang = window.sessionStorage.getItem('language');

    languagesJson.forEach(el => {
        let obj = el;
        obj.flag = require('../images/flags/' + el.flag).default;
        languages.list.push(obj);

        if(el.default === true) {
            if(selectedLang === null || selectedLang === 'null') {
                window.sessionStorage.setItem('language', JSON.stringify(el));
                languages.selected = el;
            }else {
                languages.selected = JSON.parse(window.sessionStorage.getItem('language'));
            }
        }
    });

    return languages;
}

const getLanguage = () => {
    let siteContent = {};
    let lang = window.sessionStorage.getItem('language');

    if(lang === null || lang === 'null') {
        initLanguages();
    }

    lang = window.sessionStorage.getItem('language');
    const sel = JSON.parse(lang);

    try {
        siteContent = require(`./content_${sel.lang}.json`);
    }catch (ex) {
        window.sessionStorage.setItem('language', null);
        siteContent = getLanguage();
    }
    return siteContent;
}

const languages = initLanguages();
const siteContent = getLanguage();

export { languages, siteContent };