import React from 'react'

const Hamburger = ({id, toggleHamburger, onSidenavToggle}) => {

    return (
        <div id={id} className={toggleHamburger ? 'hmb_active' : null} onClick={() => onSidenavToggle()}>
            <div />
            <div />
            <div />
        </div>
    )
}

export default Hamburger;
